body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.character-card:hover {
    cursor: pointer;
}
.character-card {
    color: black;
}

h4 {
    float: left;
}
.character-list {
    padding-top: 20px;
}
.Character-searcher {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.Get-button {
    margin: 10px !important; /* important b/c semantic-ui overrides this for some reason */
}

.search-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

.App-input {
    margin: 5px;
}
.App-logo {
    margin-top: 10px;
    margin-bottom: 10px;
}
  
.App-logo img {
    max-height: 20vh;
}

.App-header {
    background-color: #202838;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    /* font-size: calc(10px + 2vmin); */
    color: white;
    text-align: center;
}

a {
    color: white;
}
